import { reqShopCartList,reqDeleteCartById,reqUpdateCheckedById } from "@/api"
//search模块化的小仓库
//state：仓库存储数据的地方
const state = {
    cartList:[]
}
//mutations：修改state的唯一手段
const mutations = {
    GETSHOPLIST(state,cartList){
        state.cartList = cartList;
    }
}
//actions：处理action，可以书写自己的业务逻辑，也可以处理异步
const actions = {
    //获取购物车列表的数据
    async getShopList({commit}){
        let result = await reqShopCartList();
        if(result.code==200){
            commit('GETSHOPLIST',result.data);
        }
    },
    //删除购物车某一个产品
    async deleteCartListBySkuId({commit},skuId){
        let result =await reqDeleteCartById(skuId);
        if(result.code==200){
            return 'ok';
        }else{
            return Promise.reject(new Error('faile'));
        }
    },
    //修改购物车某一个产品的选中状态
    async updateCheckedById({commit},{skuId,isChecked}){
        let result =await reqUpdateCheckedById(skuId,isChecked);
        if(result.code==200){
            return 'ok';
        }else{
            return Promise.reject(new Error('faile'));
        }
    },
    //删除全部勾选的产品
    deleteAllCheckedCart({dispatch,getters}){
        //context小仓库(上下文属性))，commit【提交mutations修改state】 getters【计算属性】 dispach【派发action】 state【当前仓库数据】
        //获取购物车全部的产品
        let PromiseAll = [];
        getters.cartList.cartInfoList.forEach(item => {
            let promise= item.isChecked==1?dispatch('deleteCartListBySkuId',item.skuId):'';
            PromiseAll.push(promise);
        });
        //只要全部p1p2...都成功，返回结构成功
        //如果有一个失败，返回失败结果
        return Promise.all(PromiseAll);
    },
    //修改全部产品的状态
    updateAllCartIsChecked({dispatch,state},isChecked){
        //数组
        let PromiseAll = [];
        state.cartList[0].cartInfoList.forEach(item => {
            let promise = dispatch('updateCheckedById',{
                skuId:item.skuId,
                isChecked
            });
            PromiseAll.push(promise);
        });
        //最终返回结果
        return Promise.all(PromiseAll);
    }
}
//getters：理解为计算属性，用于简化仓库数据，让组件获取仓库数据更加方便
//可以把我们将来需要用到的数据简化一下【将来组件在获取数据的时候就简单了】
const getters = {
    cartList(state){
        return state.cartList[0] || {}
    }
}


export default {
    state,
    mutations,
    actions,
    getters
}