import { reqGoodsInfo,reqAddOrUpdateShopCart } from "@/api"
//封装的游客身份模块uuid--->生成一个随机字符串（不能在变了）
import{getUUID} from '@/utils/uuid_token'
//search模块化的小仓库
//state：仓库存储数据的地方
const state = {
    goodsInfo: {},
    uuid_token:getUUID()
}
//mutations：修改state的唯一手段
const mutations = {
    GETGOODSINFO(state, goodsInfo) {
        state.goodsInfo = goodsInfo;
    }
}
//actions：处理action，可以书写自己的业务逻辑，也可以处理异步
const actions = {
    //获取产品信息的action
    async getGoodsInfo({ commit }, skuId) {
        let result = await reqGoodsInfo(skuId);
        if (result.code == 200) {
            commit("GETGOODSINFO", result.data);
        }
    },
    //将产品添加到购物车中
    async addOrUpdateShopCart({commit},{skuId,skuNum}){
        //加入购物车返回的解构赋值
        //加入购物车之后（发请求），前台将参数带给服务器
        //服务器写入数据后，并没有返回其他数据，只是返回code=200，代表这次操作成功
        let result = await reqAddOrUpdateShopCart(skuId,skuNum);
        //因为服务器没有返回其余数据所以不需要进行仓库三连环存储数据
        if(result.code == 200){
            return 'ok'
        }else{
            //代表加入购物车失败
            return Promise.reject(new Error('faile'));
        }
    }
}
//getters：理解为计算属性，用于简化仓库数据，让组件获取仓库数据更加方便
//可以把我们将来需要用到的数据简化一下【将来组件在获取数据的时候就简单了】
const getters = {
    //路径导航简化的信息
    categoryView(state){
        //比如：state.goodsInfo初始状态是空对象，空对象的categoryView属性值是undefinded
        //当前计算出来的至少是个空对象就不会报错了
        return state.goodsInfo.categoryView || {};
    },
    //简化产品信息的数据
    skuInfo(state){
        return state.goodsInfo.skuInfo || {};
    },
    //产品售卖属性的简化
    spuSaleAttrList(state){
        return state.goodsInfo.spuSaleAttrList || [];
    }
}


export default {
    state,
    mutations,
    actions,
    getters
}