//配置路由的地方
import Vue from 'vue'
import VueRouter from 'vue-router'
//导入路由配置
import routes from './routes';
//引入store
import store from '@/store';

//使用VueRouter插件
Vue.use(VueRouter)


//先将VueRouter的原型对象中的push，replace保存一份
let orginPush = VueRouter.prototype.push;
let orginReplace = VueRouter.prototype.replace;
//重写push和replace
//第一个参数：告数原来push方法往哪跳
//第二个参数：成功回调
//第三个参数：失败回调
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        orginPush.call(this, location, resolve, reject);
    } else {
        orginPush.call(this, location, () => { }, () => { });
    }
}
VueRouter.prototype.replace = function (location, resolve, reject) {
    if (resolve && reject) {
        orginReplace.call(this, location, resolve, reject);
    } else {
        orginReplace.call(this, location, () => { }, () => { });
    }
}

//配置路由
let router =  new VueRouter({
    routes,
    scrollBehavior(to, from, savedPosition) {
        //y=0代表滚动条在最上方
        return { y: 0 }
    }
})


//全局守卫：前置守卫（在路由跳转之前判断）
router.beforeEach(async (to,from,next)=>{
    //to：可以获取到你要跳转到那个路由
    //from :可以获取到你是从哪个路由而来的信息
    //next:放行函数 next()放行 next(path)放行到指定路由 next(false)中断当前导航
    //用户登录了才会有token,未登录不会有token
    let token = store.state.user.token;
    //用户信息
    let name = store.state.user.userInfo.name;
    // next()
    if(token){
        //用户登录了就别想去login页面
        if(to.path=='/login'||to.path=='/register'){
            next('/home');
        }else{
            //登录了但是去的不是login[home|search|detail|shopcart]
            //如果有用户名
            if(name){
                next();
            }else{
                //没有用户信息
                try {
                    //获取用户信息
                  await store.dispatch('getUserInfo');
                  next();
                  } catch (error) {
                    // token失效了
                    //清除token
                    store.dispatch('userLogout');
                    next('/login');
                  }
            }
        }
    }else{
        //未登录不能去交易相关【pay|paysuccess】,不能去个人中心
        let toPath = to.path;
        if(toPath.indexOf('/trade')!=-1||toPath.indexOf('/pay')!=-1||toPath.indexOf('/center')!=-1){
            next('/login?redirect='+toPath)
        }else{
            next()
        }

    }
});

export default router;