<template>
  <div class="fr page">
    <div class="sui-pagination clearfix">
      <ul>
        <li class="prev disabled">
          <a
            :style="pageNo == 1 ? 'pointer-events:none;' : ''"
            @click="$emit('getPageNo', pageNo - 1)"
            >«上一页</a
          >
        </li>
        <li :class="{ active: pageNo == 1 }">
          <a
            v-if="startNumAndEndNumber.start > 1"
            @click="$emit('getPageNo', 1)"
            >1</a
          >
        </li>
        <li class="dotted" v-if="startNumAndEndNumber.start > 2">
          <span>...</span>
        </li>
        <li
          v-for="(page, index) in startNumAndEndNumber.end"
          :key="index"
          :class="{ active: pageNo == page }"
        >
          <a
            v-if="page >= startNumAndEndNumber.start"
            @click="$emit('getPageNo', page)"
            >{{ page }}</a
          >
        </li>
        <li class="dotted" v-if="startNumAndEndNumber.end < totalPage - 1">
          <span>...</span>
        </li>
        <li :class="{ active: pageNo == totalPage }">
          <a
            v-if="startNumAndEndNumber.end < totalPage"
            @click="$emit('getPageNo', totalPage)"
            >{{ totalPage }}</a
          >
        </li>
        <li class="next">
          <a
            :style="pageNo == totalPage ? 'pointer-events:none;' : ''"
            @click="$emit('getPageNo', pageNo + 1)"
            >下一页»</a
          >
        </li>
      </ul>
      <div><span>共{{total}}条&nbsp;</span></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageNo", "pageSize", "total", "continues"],
  computed: {
    //总共多少页
    totalPage() {
      //向上取整
      return Math.ceil(this.total / this.pageSize);
    },
    startNumAndEndNumber() {
      const { continues, pageNo, totalPage } = this;
      let start = 0,
        end = 0;
      //如果总页数没有连续页码多
      if (continues > totalPage) {
        start = 1;
        end = totalPage;
      } else {
        //总页码大于连续页码
        start = pageNo - parseInt(continues / 2);
        end = pageNo + parseInt(continues / 2);
        //如果pageNo（当前页码为1或2）start为-1或0   要进行纠正
        if (start < 1) {
          start = 1;
          end = continues;
        }
        //end大于总页码  纠正
        if (end > totalPage) {
          end = totalPage;
          start = totalPage - continues + 1;
        }
      }
      return { start, end };
    },
  },
};
</script>

<style lang="less" scoped>
.page {
  // width: 733px;
  height: 66px;
  overflow: hidden;
  float: right;

  .sui-pagination {
    margin: 18px 0;

    ul {
      margin-left: 0;
      margin-bottom: 0;
      vertical-align: middle;
      // width: 490px;
      float: left;

      li {
        line-height: 18px;
        display: inline-block;

        a {
          position: relative;
          float: left;
          line-height: 18px;
          text-decoration: none;
          background-color: #fff;
          border: 1px solid #e0e9ee;
          margin-left: -1px;
          font-size: 14px;
          padding: 9px 18px;
          color: #333;
        }

        &.active {
          a {
            background-color: skyblue;
            color: #e1251b;
            border-color: #fff;
            cursor: default;
          }
        }

        &.prev {
          a {
            background-color: #fafafa;
          }
        }

        &.disabled {
          a {
            color: #999;
            cursor: default;
          }
        }

        &.dotted {
          span {
            margin-left: -1px;
            position: relative;
            float: left;
            line-height: 18px;
            text-decoration: none;
            background-color: #fff;
            font-size: 14px;
            border: 0;
            padding: 9px 18px;
            color: #333;
          }
        }

        &.next {
          a {
            background-color: #fafafa;
          }
        }
      }
    }

    div {
      color: #333;
      font-size: 14px;
      float: right;
      width: 241px;
    }
  }
}
</style>