import { reqGetSearchInfo } from "@/api"
//search模块化的小仓库
//state：仓库存储数据的地方
const state = {
    searchList:{}
}
//mutations：修改state的唯一手段
const mutations = {
    GETSEARCHLIST(state,searchList){
        state.searchList = searchList;
    }
}
//actions：处理action，可以书写自己的业务逻辑，也可以处理异步
const actions = {
    //获取search模块数据
    async getSearchList({ commit }, params = {}) {
        //当前这个reqGetSearchInfo接口，给服务器传对象至少是一个空对象
        //params形参，是当用户派发action的时候第二个参数是传递过来的，至少是一个空对象
        let result = await reqGetSearchInfo(params);
        if (result.code == 200) {
            commit("GETSEARCHLIST", result.data);
        }
    },
}
//getters：理解为计算属性，用于简化仓库数据，让组件获取仓库数据更加方便
//可以把我们将来需要用到的数据简化一下【将来组件在获取数据的时候就简单了】
const getters = {
    //当前形参state就是当前仓库中的state不是大仓库里的state
    goodsList(state){
        //假如网络不给力|没有网state.searchList.goodList应该返回一个空数组而不是undefined
        return state.searchList.goodsList || [] ;
    },
    trademarkList(state){
        return state.searchList.trademarkList || [] ;
    },
    attrsList(state){
        return state.searchList.attrsList || [] ;
    }
}


export default {
    state,
    mutations,
    actions,
    getters
}